<template>
	<view class="content">
		<view class="logo">
			<image class="img" mode="widthFix" src="../../static/image/logo-jxw.png">
			</image>
			<image class="img" mode="widthFix" src="../../static/image/slogan.png">
			</image>
		</view>
		
        <view class="btn" @click="authUserInfo">
