			<button open-type="getPhoneNumber" @getphonenumber="getPhoneNumber">电话授权</button>
		</view>
	</view>
</template>

<script>
	import {mapActions,mapGetters } from 'vuex'
	import {
		// wxLogin,
		// wxInfo,
		wxPhone
	} from '@/utils/request/manage.js'
	export default {
		data() {
			return {
				userInfo: {},
				hasUserInfo: false,
