				console.log(userinfo)
				const {data: userPhone} = wxPhone(token,userinfo.wxOpenid,userinfo.sessionKey,e.detail.encryptedData,e.detail.iv)
				uni.navigateBack({
					
				})
				
			}
		},
	}
</script>

